@import url('https://fonts.googleapis.com/css2?family=Advent+Pro:ital,wdth,wght@0,151.8,100..900;1,151.8,100..900&family=Silkscreen:wght@400;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Tektur:wght@400;500&display=swap');
*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}
body::-webkit-scrollbar {
  display: none;
  /* Hides scrollbar for WebKit-based browsers */
}
html , body{
  height: 100%;
  width: 100%;
  font-family: 'tektur';
}
/* navbar css */
.navbar{
  background: linear-gradient(75deg, rgb(168, 255, 252), aqua) !important;
  font-family: 'tektur';
  font-weight: 400;
  font-size: 20px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0px 8px 6px -6px rgb(120, 113, 113);
}
.nav-link{
  font-weight: 200 !important;
}
.active{
  border-bottom: 2px solid black;
}
.navbar-brand{
  font-family: 'silkscreen';
  font-weight: 500;
  font-size: 30px !important;
  letter-spacing: 1.5px;
}
/* footer css */
.footer{
  width: 100%;
  position: absolute;
  bottom: 0;
  background: linear-gradient(90deg, rgb(168, 255, 252), aqua);
  color: #000000;
  border-top-left-radius: 125px;
  border-top-right-radius: 125px;
  padding: 5px 0 5px;
  font-size: 23px;
  line-height: 4px;
  box-shadow: 0px 8px 6px 8px rgb(120, 113, 113);
}
.footer a{
  font-size: 30px;
  text-decoration: none;
  color: rgb(126, 121, 121);
  padding: 10px;
}
.footer a:hover{
  color: rgb(71, 64, 64) ;
}
.text-center{
  color: rgb(134, 65, 65);
  font-size: 18px;
  font-weight: 500;
  font-family:  'tektur';
}
/* Pagenotfound css */
.pnf{
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.pnf-title{
  font-size: 100px;
  font-weight: 700;
}
.pnf-heading{
  font-weight: normal;
}
.pnf-btn{
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top:10px ;
}
.pnf-btn:hover{
  color: white;
  background-color: black;
}
/* contact css */
.contactus {
  margin-top:20px ;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
#set h1{
  cursor: pointer;
  font-size: 35px;
}
#set p{
  cursor: pointer;
  font-size: 20px;
  font-family: 'tektur';
}
.contactus img{
  height: 70vh;
}
/* aboutcss */
#about p{
  font-size: 18px !important;
  font-family: tektur; 
}
#about h1 {
  cursor: pointer;
  font-size: 35px;
}
/* policycss */
#policy p{
  font-size: 16px;
  font-family: tektur;
}
#policy h1 {
  cursor: pointer;
  font-size: 35px;
}
/* register css */
.register{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  min-height: 85vh;
}
h1,h2,h4 ,h5,h3{
  font-family: 'tektur';
}
.btn{
  font-family: 'tektur';
}
.product-link{
  text-decoration: none !important;
  color: black;
}