/* Footer Styles */
.footer {
    position: fixed;
    /* Fix the position */
    bottom: 0;
    /* Stick to the bottom */
    left: 0;
    width: 100%;
    /* Full width */
    background-color: #f8f9fa;
    /* Optional styling */
    padding: 10px 0;
    box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
    /* Optional shadow */
    z-index: 1000;
    transition: transform 0.3s ease-in-out;
    /* Ensure it's above other content */
}
.footer.hidden {
    transform: translateY(100%);
}

.footer.visible {
    transform: translateY(0);
}

.para {
    margin: 0;
}

.text-center {
    text-align: center;
}