/* part-1 */
@import url('https://fonts.googleapis.com/css2?family=Tektur:wght@400;500&display=swap');

.part1 {
    width: 100%;
    height: 100vh;
    background-color: #000;
    overflow: hidden;
}

.content-part1 {
    display: flex;
    align-items: start;
    justify-content: center;
    width: 100%;
    height: 100vh;

}

.rotatediv {
    gap: 3vw;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 200%;
    height: 200vh;
    background-color: #000;
    /* scale: 0.5; */
}

.row-div {
    display: flex;
    flex-direction: column;
    gap: 3vw;
    width: calc(200vw/5);
    height: 100%;
}

.imgdiv {
    width: 100%;
    height: calc(100%/4);
    border-radius: 10px;
    overflow: hidden;
}

.imgdiv img {
    width: 100%;
    height: 100%;
}

#row-div-2 {
    margin-top: -5%;
}

#row-div-3 {
    margin-top: -8%;
}

#row-div-4 {
    margin-top: -12%;
}

#row-div-5 {
    margin-top: -15%;
}

.overlay {
    width: 100%;
    height: 100vh;
    background-color: #00000000;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 5vw;
}

.overlay h1 {
    font-weight: 500;
    color: white;
    letter-spacing: 12px;
    opacity: 0;
}

/* part-6 */
.part6 {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;
    background: linear-gradient(to bottom, #000 , #fff);
    
}

.our-work-txt {
    position: absolute;
    width: 90vw;
    height: 0vh;
    z-index: 9;
}

#Our {
    position: absolute;
    top: 0;
    left: 35%;
    transform: translate(-20%, -50%);
    color: #fff;
    font-size: 5vw;
}

#Work {
    position: absolute;
    bottom: 0;
    right: 35%;
    transform: translate(20%, 50%);
    color: #fff;
    font-size: 5vw;
}

.our-work-div {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: 60vw;
    height: 0vh;
    background-color: #fff;
}

.scroll-work {
    width: 100%;
    height: 60vh;
    background-color: #919191;
    overflow: hidden;
}

.scroll-img {
    width: 100%;
    margin-top: 0%;
    transition: all cubic-bezier(0.19, 1, 0.22, 1)1s;
}

.scroll-img img {
    width: 100%;
}

#demo {
    position: absolute;
    bottom: 7%;
    padding: 1.5vw 3vw;
    background-color: #d1d1d1;
    font-size: 0.8vw;
    border: none;
    border-radius: 50px;
}
.part7 {
    background-color: white;
    position: relative;
    z-index: 10;
}
.card {
    height: 520px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    overflow: hidden;
}
.card-img-top {
    height: 280px;
    object-fit: cover;
}
.card-body {
    flex-grow: 1;
    /* Allow the card body to grow and fill available space */
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    /* Ensure buttons are at the bottom */
}
.card-body .btn {
    margin-top: 10px;
    /* Add some space between buttons */
}
.ant-checkbox-wrapper {
    font-family: 'Tektur', cursive !important;
}
