.category {
    flex-wrap: wrap;
    margin-top: 80px !important;
}

.category .card {
    background-color: rgba(128, 128, 128, 0.097);
    width: 18rem;
}

.category .card-name-price {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.category .card-price {
    color: green;
    font-weight: bold;
}

.category button {
    border-radius: 0;
    width: 100%;
    border-top-left-radius: 5px;
    border-bottom-right-radius: 5px;
    font-size: 14px;
    font-weight: bold;
}

.category .card-text {
    color: rgb(90, 89, 89);
}

.category .card img:hover {
    transform: scale(0.9);
}

.category .card-img-top {
    height: 300px !important; }
.categories-container {
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            /* 3 columns */
            gap: 30px;
            margin-top: 50px;
        }
    
        .category-box {
            height: 200px;
            background-color: #31daf856;
            border-radius: 8px;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: all 0.3s ease;
        }
    
        .category-box:hover {
            background-color: #41ecff;
            transform: scale(1.02);
        }
    
        .category-link {
            font-size: 20px;
            font-weight: bold;
            color: #000000;
            background-color: #fffafad2;
            padding: 12px 24px;
            border-radius: 5px;
            text-decoration: none;
            transition: all 0.3s ease;
        }
    
        .category-link:hover {
            background-color: #000000;   
        color: #fff;
        transform: scale(1.02);
    }